html {
  font-size: 18px; }

body {
  display: flex;
  min-height: 100vh;
  flex-direction: column;
  background: linear-gradient(90deg, rgba(0, 0, 0, 0.75) 0%, rgba(0, 0, 0, 0.75) 100%), url("../img/bg.jpg") center center no-repeat fixed;
  background-size: cover, cover;
  color: white; }

main {
  flex: 1; }

.outer {
  max-width: 1140px; }

footer > p {
  margin: 0; }

h6 {
  font-size: 17px;
  font-weight: normal;
  margin: 0;
  text-transform: uppercase;
  color: #fff;
  opacity: 0; }

a {
  text-decoration: none;
  color: #38fedb; }
  a:visited {
    color: #38fedb; }
  a:hover {
    text-decoration: underline; }

.span {
  border-radius: 4px;
  padding: 0 0.2rem; }
  .span--a {
    background-color: #fff;
    color: black; }
  .span--b {
    background-color: #dc3545;
    color: white; }

.text-indent {
  text-indent: 1rem; }
  .text-indent:last-child {
    margin-bottom: 0; }

.text-bold {
  font-weight: bold; }

.list-counter-square {
  list-style: none;
  counter-reset: list;
  margin: 0;
  overflow: hidden; }

.list-counter-square > ol {
  position: relative;
  display: block;
  height: 2rem;
  line-height: 2rem;
  margin-left: 2rem;
  margin-bottom: .6rem;
  padding-left: .8rem;
  padding-right: .5rem;
  white-space: nowrap; }

.list-counter-square > ol:last-child {
  margin-bottom: 1rem; }

.list-counter-square > ol::before {
  content: counter(list);
  counter-increment: list;
  position: absolute;
  left: -2rem;
  top: 0;
  bottom: 0;
  width: 2rem;
  height: 2rem;
  text-align: center;
  border: 1px solid white;
  border-radius: 50%;
  box-sizing: content-box; }

.opacity-1 {
  opacity: 1; }

.opacity-0 {
  opacity: 0; }

.hidden {
  display: none !important; }

@keyframes anim-in {
  from {
    opacity: 0; }
  to {
    opacity: 1; } }

h6, .btn-round, .btn-restart, .card, .btn-close, .btn-verdict {
  transition: color .15s ease-in-out, background-color .15s ease-in-out, border-color .15s ease-in-out, box-shadow .15s ease-in-out, opacity .15s ease-in-out; }

/*
|--------------------------------------------------------------------------
| Control
|--------------------------------------------------------------------------
*/
.round-container {
  text-align: center;
  position: relative;
  background-color: #1b1b1b;
  border: 16px solid #1b1b1b;
  border-bottom: 32px solid #1b1b1b;
  border-top: 32px solid #1b1b1b; }

.btn-round {
  position: absolute;
  top: 50%;
  right: 50%;
  transform: translate(50%, -50%);
  border-radius: 50%;
  width: 100px;
  height: 100px;
  border: 6px #7a1f28 solid;
  background-color: #dc3545;
  color: white;
  text-transform: uppercase; }
  .btn-round:hover {
    background-color: #c82333; }
  .btn-round-num {
    font-size: 1.8rem;
    line-height: 100%; }

.btn-restart {
  position: absolute;
  top: 0;
  right: 0;
  background: url("../img/update-arrows.png");
  width: 32px;
  height: 32px;
  opacity: .6;
  border: none; }
  .btn-restart:hover {
    opacity: 1; }

/*
|--------------------------------------------------------------------------
| Card
|--------------------------------------------------------------------------
*/
.card-containter {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(330px, 1fr));
  grid-gap: 16px;
  justify-items: center;
  background-color: #bfbfbf;
  border: 16px solid #1b1b1b;
  border-top: 0; }

.card {
  display: flex;
  flex-direction: column;
  user-select: none;
  animation: .5s anim-in ease-in-out;
  border: none;
  color: black;
  overflow: hidden; }

.card-control-container {
  padding: 6px;
  z-index: 1;
  display: inline-flex;
  align-items: center;
  justify-content: space-between; }

.btn-close {
  height: 32px;
  width: 32px;
  background-size: 20px;
  box-sizing: border-box; }
  .btn-close:focus {
    box-shadow: none; }

.card-content-container {
  background-color: #fff;
  padding: 16px;
  position: relative; }

.card-container-indent {
  margin-bottom: 16px; }
  .card-container-indent:last-child {
    margin-bottom: 0; }

.card-status-container {
  display: flex;
  width: 100%;
  background: #bfbfbf;
  border-radius: 4px; }

.card-status:first-child, .btn-verdict:first-child {
  border-top-left-radius: 4px;
  border-bottom-left-radius: 4px; }

.card-status:last-child, .btn-verdict:last-child {
  border-top-right-radius: 4px;
  border-bottom-right-radius: 4px; }

.card-status:not(:first-child), .btn-verdict:not(:first-child) {
  margin-left: -1; }

.card-status {
  font-size: 17px !important;
  color: #212121;
  font-weight: bold;
  text-align: center;
  width: 38px;
  height: 38px;
  line-height: 38px;
  border: 1px solid rgba(0, 0, 0, 0.125);
  animation: .2s anim-in ease-in-out;
  cursor: pointer; }

.card-status-active {
  position: relative; }
  .card-status-active::before {
    width: 100%;
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    border: 18px solid transparent;
    border-top: 10px solid #5b5b5b; }

.btn-status {
  width: 102px;
  height: 38px;
  color: black;
  font-size: 17px !important;
  background-color: #bfbfbf;
  border: 1px solid rgba(0, 0, 0, 0.125);
  overflow: hidden;
  word-wrap: normal; }
  .btn-status:hover {
    color: #fff;
    background-color: #5b5b5b;
    border-color: #5b5b5b; }
  .btn-check:focus + .btn-status, .btn-status:focus {
    box-shadow: none;
    background-color: #5b5b5b; }
  .btn-check:checked + .btn-status,
  .btn-check:active + .btn-status, .btn-status:active, .btn-status.active {
    color: #fff;
    background-color: #5b5b5b;
    box-shadow: none;
    border-color: #5b5b5b; }
    .btn-check:checked + .btn-status:focus,
    .btn-check:active + .btn-status:focus, .btn-status:active:focus, .btn-status.active:focus {
      box-shadow: none; }

.card-verdict-container {
  display: flex;
  width: 100%; }

.btn-verdict {
  flex-grow: 1;
  position: relative;
  height: 38px;
  background: #bfbfbf;
  border: 1px solid rgba(0, 0, 0, 0.125); }
  .btn-verdict:hover {
    background-color: #5b5b5b; }

/* uncss:ignore start */
.btn-overlay-killed:hover::before {
  background-image: url("../img/killed.png"); }

.btn-overlay-killed::before {
  background-image: url("../img/killed-w.png"); }

.btn-overlay-ejected:hover::before {
  background-image: url("../img/ejected.png"); }

.btn-overlay-ejected::before {
  background-image: url("../img/ejected-w.png"); }

.btn-overlay-impostor:hover::before {
  background-image: url("../img/impostor.png"); }

.btn-overlay-impostor::before {
  background-image: url("../img/impostor-w.png"); }

.btn-overlay-crewmate:hover::before {
  background-image: url("../img/crewmate.png"); }

.btn-overlay-crewmate::before {
  background-image: url("../img/crewmate-w.png"); }

.btn-overlay-killed:hover::before, .btn-overlay-killed::before, .btn-overlay-ejected::before, .btn-overlay-impostor::before, .btn-overlay-crewmate::before, .overlay-killed::before, .overlay-ejected::before, .overlay-impostor::before, .overlay-crewmate::before, .overlay-close::before, .color-close:hover::before, .color-closed::before {
  content: "";
  position: absolute;
  background-size: contain;
  background-repeat: no-repeat;
  background-position-x: center;
  background-position-y: center;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0; }

.overlay-killed::before, .overlay-ejected::before, .overlay-impostor::before, .overlay-crewmate::before, .overlay-close::before {
  animation: .2s anim-in ease-in-out;
  z-index: 2; }

.overlay-killed::before {
  background: url("../img/killed.png") center center no-repeat, rgba(0, 0, 0, 0.65); }

.overlay-ejected::before {
  background: url("../img/ejected.png") center center no-repeat, rgba(0, 0, 0, 0.65); }

.overlay-impostor::before {
  background: url("../img/impostor.png") center center no-repeat, rgba(0, 0, 0, 0.65); }

.overlay-crewmate::before {
  background: url("../img/crewmate.png") center center no-repeat, rgba(0, 0, 0, 0.65); }

.overlay-close::before {
  background: rgba(0, 0, 0, 0.65) url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 16 16' fill='%23000'%3e%3cpath d='M.293.293a1 1 0 011.414 0L8 6.586 14.293.293a1 1 0 111.414 1.414L9.414 8l6.293 6.293a1 1 0 01-1.414 1.414L8 9.414l-6.293 6.293a1 1 0 01-1.414-1.414L6.586 8 .293 1.707a1 1 0 010-1.414z'/%3e%3c/svg%3e") center/1em auto no-repeat; }

/* uncss:ignore */
.color-container {
  display: flex;
  flex-wrap: wrap;
  width: 264px; }

.color-close {
  width: 44px;
  height: 44px;
  position: relative; }
  .color-close::before {
    cursor: pointer;
    opacity: 0.65;
    animation: .2s anim-in-close ease-in-out; }

@keyframes anim-in-close {
  from {
    opacity: 0; }
  to {
    opacity: 0.65; } }

.color-close:hover::before, .color-closed::before {
  background-size: contain !important;
  border: 6px solid transparent;
  background: url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 16 16' fill='%23000'%3e%3cpath d='M.293.293a1 1 0 011.414 0L8 6.586 14.293.293a1 1 0 111.414 1.414L9.414 8l6.293 6.293a1 1 0 01-1.414 1.414L8 9.414l-6.293 6.293a1 1 0 01-1.414-1.414L6.586 8 .293 1.707a1 1 0 010-1.414z'/%3e%3c/svg%3e") center/1em auto no-repeat;
  background-color: rgba(0, 0, 0, 0.125); }

.color-closed::before {
  cursor: initial;
  opacity: .9;
  animation: none; }
